.development__title {
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.14;
  text-align: center;
  letter-spacing: 0.04em;
  color: $cl-menu-bg;
  margin-bottom: 60px;

  @include tab {
    margin-bottom: 60px;
  }

  @include pc {
    font-size: 58px;
    margin-bottom: 80px;
  }
  &-description {
    margin-bottom: 15px;
    font-family: Titan One;
    font-size: 18px;
    line-height: 1.14;
    text-align: center;
    letter-spacing: 0.04em;
    color: $cl-primary;

    @include pc {
      font-size: 30px;
    }
  }
}

.development__list {
  display: flex;
  justify-content: space-between;
}

.development-img__thumb {
  border: $cl-menu-bg solid 2px;
  border-radius: 50%;
  height: 100px;
  width: 100px;
}
